<template>
  <div class="dialog">
    <div v-if="showDialog" class="overlay">
      <div class="dialog-box">
        <div class="dialog-header">
          <span class="dialog-title">{{ title }}</span>
          <button @click="closeDialog" class="close-button">X</button>
        </div>
        <div class="dialog-content">
          <div class="word-info">
            <div class="word">
              {{ word.wordHead }}
            </div>
            <div class="pronunciation">
              美：/{{ word.content.ukphone }}/ 英：/{{ word.content.usphone }}/
            </div>
          </div>
          <div class="definition">
            <strong>释义:</strong>
            {{ word.content.trans[0].tranCn }}
          </div>
          <div class="example">
            <strong>例句:</strong>
            {{ word.content.sentence.sentences[0].sContent }}
            {{ word.content.sentence.sentences[0].sCn }}
          </div>
          <div class="example">
            <strong>助记:</strong>
            {{ word.content.remMethod.val }}
          </div>
        </div>
        <div class="dialog-footer">
          <button @click="closeDialog">已记住</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: "提示",
    },
  },
  data() {
    return {};
  },
  methods: {
    closeDialog() {
      this.$store.showDialog = false;
    },
  },
  computed: {
    showDialog() {
      return this.$store.showDialog;
    },
    word() {
      return this.$store.game.currentWord.content.word;
    },
  },
};
</script>

<style scoped>
/* 子组件中只定义局部样式 */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.dialog-box {
  background: #fff;
  border: 1px solid #ccc;
  padding: 20px;
  width: 400px;
  max-width: 80%;
  box-sizing: border-box;
}

.dialog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.dialog-title {
  font-size: 1.2em;
}

.close-button {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.2em;
  color: #888;
}

.dialog-content {
  margin-bottom: 20px;
}

.dialog-footer {
  text-align: right;
}
</style>
