<template>
  <div class="container" :class="{ 'is-show': !isShow }">
    <input :checked="!inputChecked" class="checkbox" type="checkbox" disabled />
    <div class="mainbox">
      <div class="iconContainer">✍️</div>
      <input
        ref="search"
        class="search_input"
        placeholder="请拼写！"
        type="text"
        v-model="inputValue"
        @input="handleInput"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    inputChecked: Boolean,
  },
  data() {
    return {
      inputValue: null,
    };
  },
  watch: {
    "$store.currentWord": function (newVal) {
      if (newVal) {
        console.log(this.$store.game.currentWord);
        console.log("尝试获取焦点");
        this.searchDisabled = !!this.$store.game.currentWord;
        this.$nextTick(() => {
          this.$refs.search.focus();
        });
      } else {
        this.searchDisabled = false;
      }
    },
  },
  methods: {
    handleInput(e) {
      console.log(e.target.value);
      if (e.target.value.trim() === this.$store.game.currentWord) {
        this.$store.score.currentScore += 20;
        this.$store.inputIsChecked = true;
        this.$store.game.currentWord = "";
        this.$refs.search.blur();
        this.inputValue = ""; // 清空输入框的内容
        console.log("11111");

        // 触发自定义事件，通知父组件拼写成功
        this.$emit("spelling-success");
      }
    },
  },
  computed: {
    isShow() {
      return this.$store.game.playing;
    },
  },
};
</script>

<style>
.container {
  position: relative;
  box-sizing: border-box;
  width: fit-content;
}
.container.is-show {
  display: none;
}

.mainbox {
  box-sizing: border-box;
  position: relative;
  width: 460px;
  height: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 1rem;
  background-color: var(--theme-card-bg);
  transition: all 0.3s ease;
}

.checkbox:focus {
  border: none;
  outline: none;
}

.checkbox:checked ~ .mainbox {
  width: 100px;
}

.checkbox:checked ~ .mainbox .search_input {
  width: 0;
  height: 0;
}

.checkbox {
  margin: 0;
  width: 100px;
  height: 100px;
  left: 0;
  position: absolute;
  z-index: 99;
  cursor: not-allowed;
  appearance: none;
}

.search_input {
  box-sizing: border-box;
  height: 100%;
  width: 220px;
  background-color: transparent;
  border: none;
  outline: none;
  padding-bottom: 4px;
  font-size: 50px;
  color: var(--theme-font-main);
  transition: all 0.3s ease;
}

.search_input::placeholder {
  color: var(--theme-font-secondary);
}

.iconContainer {
  width: 100px;
  box-sizing: border-box;
  margin-left: 15px;
  font-size: 50px;
}
</style>
