<template>
  <basic-card
    :isFlipped="isFlipped"
    :isDisable="isDisabled"
    @flip-card="$emit('flip-card', word)"
  >
    <template v-slot:front>
      <div>🔊</div>
    </template>
    <template v-slot:back>
      <div>
        <audio
          controls
          :src="americanAudioSrc"
          ref="audioAmericanRef"
          style="display: none"
        ></audio>
        <audio
          controls
          :src="britishAudioSrc"
          ref="audioBritishRef"
          style="display: none"
        ></audio>
        <div class="audio" @click.stop="playAmericanAudio">
          美[{{ word.content.word.content.usphone }}]
        </div>
        <div class="audio" @click.stop="playBritishAudio">
          英[{{ word.content.word.content.ukphone }}]
        </div>
      </div>
    </template>
  </basic-card>
</template>

<script>
import basicCard from "../game-card/basic-card.vue";
export default {
  name: "audio-card",
  components: { basicCard },
  props: {
    word: Object,
    isFlipped: Boolean,
    isDisabled: Boolean,
  },

  computed: {
    americanAudioSrc() {
      return `http://dict.youdao.com/dictvoice?audio=${this.word.headWord}&type=1`;
    },
    britishAudioSrc() {
      return `http://dict.youdao.com/dictvoice?audio=${this.word.headWord}&type=2`;
    },
  },

  methods: {
    playAmericanAudio() {
      this.$refs.audioAmericanRef.play(); // 获取audio元素
    },
    playBritishAudio() {
      this.$refs.audioBritishRef.play(); // 获取audio元素
    },
    flip(word) {
      console.log(word);
      console.log("todo");
    },
  },
};
</script>

<style>
.audio {
  width: 80px;
  font-size: 14px;
  margin: 10px auto;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 5px;
}
</style>
