<template>
  <basic-card
    :isFlipped="isFlipped"
    @flip-card="handleCardClick"
    title="主题"
    size="small"
  >
    <template v-slot:front>
      <p>🌞</p>
    </template>
    <template v-slot:back>
      <p>🌝</p>
    </template>
  </basic-card>
</template>

<script>
import BasicCard from "../game-card/basic-card.vue";
export default {
  name: "theme-card",
  components: { BasicCard },
  data() {
    return {
      isFlipped: false,
    };
  },

  methods: {
    handleCardClick() {
      console.log(this.isFlipped);
      this.isFlipped = !this.isFlipped;
      const themeName = this.isFlipped ? "dark" : "";
      document.documentElement.setAttribute("data-theme", themeName);
    },
  },
};
</script>

