<template>
  <div>
    <basic-card
      :isFlipped="false"
      :isDisable="isDisabled"
      title="提示"
      size="small"
    >
      <template v-slot:front>
        <p @click="$store.showDialog = true">🆘</p>
      </template>
      <template v-slot:back> </template>
    </basic-card>
  </div>
</template>

<script>
import basicCard from "../game-card/basic-card.vue";

export default {
  name: "prompt-card",
  components: { basicCard },
  data() {
    return {
      showDialog: false,
    };
  },
  methods: {
    prompt() {
      this.showDialog = true;
      //提示需要减分
    },
  },
  computed: {
    isDisabled() {
      if (this.$store.game.playing && this.$store.game.currentWord) {
        return false;
      }
      return true;
    },
  },
};
</script>

