<template>
  <div class="game-board-container">
    <div class="word-card-container">
      <audio-card
        class="card"
        v-for="word in shuffledWordList"
        :key="'audio' + word.wordRank"
        :word="word"
        :isFlipped="word.isFlipped[0]"
        :isDisabled="word.isFlipped[0] || inputChecked"
        @flip-card="handleFlip(word, 0)"
      ></audio-card>
      <paraphrase-card
        class="card"
        v-for="word in wordList"
        :key="'paraphrase' + word.wordRank"
        :word="word"
        :isFlipped="word.isFlipped[1]"
        :isDisabled="word.isFlipped[1] || inputChecked"
        @flip-card="handleFlip(word, 1)"
      ></paraphrase-card>
    </div>
    <input-card
      class="input-card"
      :inputChecked="inputChecked"
      @spelling-success="handleSpellingSuccess"
    ></input-card>
  </div>
</template>

<script>
import inputCard from "./input-card.vue";
import paraphraseCard from "./paraphrase-card.vue";
import audioCard from "./audio-card.vue";

export default {
  components: {
    audioCard,
    paraphraseCard,
    inputCard,
  },
  data() {
    return {
      flippedCard: null,
      inputChecked: false,
    };
  },
  methods: {
    handleFlip(word, index) {
      this.$store.game.currentWord = word;
      console.log("当前单词" + JSON.stringify(this.$store.game.currentWord));
      for (let item of this.wordList) {
        if (item.wordRank === word.wordRank) {
          console.log(item);
          this.$set(item.isFlipped, index, !item.isFlipped[index]);
        }
      }

      // 如果之前没有翻过卡片，就记录下这张卡片
      if (!this.flippedCard) {
        this.flippedCard = { word: word, type: index };
      } else {
        // 如果之前翻过一张卡片，就和这张卡片比较
        if (this.flippedCard.word.wordRank === word.wordRank) {
          // 如果匹配，你可以在这里执行匹配时的逻辑，例如标记这两张卡片为已翻转
          this.$store.game.currentWord = this.flippedCard.word.headWord;
          this.spelling = false;
          this.inputChecked = true;
          this.flippedCard = null;

          console.log("Matched!");
        } else {
          console.log("Not Matched!");
          // 如果不匹配，你可以在这里执行不匹配时的逻辑，例如将这两张卡片翻回去
          let wordToFlip = word;
          console.log(word);
          setTimeout(() => {
            for (let item of this.wordList) {
              // 翻转第二张卡片
              if (item.wordRank === wordToFlip.wordRank) {
                console.log(item);
                this.$set(item.isFlipped, index, false);
              }
              // 翻转之前的卡片
              if (item.wordRank === this.flippedCard.word.wordRank) {
                console.log(item);
                // 使用之前的卡片的类型来决定翻转的索引
                this.$set(item.isFlipped, this.flippedCard.type, false);
              }
            }
            // 清空记录的卡片
            this.spelling = true;
            this.inputChecked = false;
            this.flippedCard = null;
          }, 600);
        }
        // 清空记录的卡片
      }
    },
    handleSpellingSuccess() {
      this.inputChecked = false;
      this.spelling = true;
    },
  },
  computed: {
    wordList() {
      return this.$store.game.wordList;
    },
    //用来打乱展示的卡片
    shuffledWordList() {
      // 使用.slice()来创建一个新数组，以防止直接修改原始数据
      return this.wordList.slice().sort(() => Math.random() - 0.5);
    },
  },
};
</script>

<style>
.game-board-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.word-card-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 每行显示 4 列 */
  gap: 20px;
}

.input-card {
  margin-top: 20px;
}
</style>
