<template>
  <basic-card
    :isFlipped="isFlipped"
    @flip-card="updateWordList"
    :isDisable="false"
    :title="title"
    size="small"
  >
    <template v-slot:front>
      <p>▶️</p>
    </template>
    <template v-slot:back>
      <p>⏹️</p>
    </template>
  </basic-card>
</template>

<script>
import demoDict from "../../dict/demo_word.json";
import BasicCard from "../game-card/basic-card.vue";
export default {
  name: "theme-card",
  components: { BasicCard },
  data() {
    return {
      title: "开始",
      isFlipped: false,
    };
  },

  methods: {
    updateWordList() {
      this.isFlipped = !this.isFlipped;
      if (this.isFlipped) {
        const shuffledItems = this.shuffleArray(demoDict);
        const randomizedItems = shuffledItems
          .slice(0, 4)
          .map((item) => ({ ...item, isFlipped: [false, false] }));
        console.log(randomizedItems);

        this.$store.game.wordList = randomizedItems;
        console.log(this.$store.game.wordList);
        this.$store.game.playing = true;
        //this.$store.cardState = ;
      } else {
        this.$store.game.playing = false;
        this.$store.game.wordList = [];
      }
      this.title = !this.isFlipped ? "开始" : "结束";
    },
    shuffleArray(array) {
      // 随机打乱数组
      return array.slice().sort(() => Math.random() - 0.5);
    },
  },
};
</script>

