<template>
  <div class="card-container">
    <div class="card" :class="[size]" @click="flipCard">
      <div
        class="card-inner"
        :class="{ 'is-flipped': isFlipped, 'is-disable': isDisable }"
      >
        <div class="card-front">
          <slot name="front"></slot>
        </div>
        <div class="card-back">
          <slot name="back"></slot>
        </div>
      </div>
    </div>
    <!-- 添加标题元素 -->
    <div v-if="title" class="title">{{ title }}</div>
  </div>
</template>

<script>
export default {
  props: {
    isFlipped: {
      type: Boolean,
      default: false,
    },
    isDisable: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: "",
    },
    size: {
      type: String,
      default: "medium",
    },
  },
  data() {
    return {};
  },
  methods: {
    flipCard() {
      if (!this.isDisable) {
        this.$emit("flip-card");
      } else {
        return;
      }
    },
  },
};
</script>

<style scoped>
.card-container {
  display: flex;
  flex-direction: column;
}
.card {
  cursor: pointer;
  background-color: transparent;

  perspective: 1000px;
  font-family: sans-serif;
}
.small {
  font-size: 32px;
  width: 60px;
  height: 60px;
}

.medium {
  font-size: 56px;
  width: 100px;
  height: 100px;
}

.card .card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.is-flipped {
  transform: rotateY(180deg);
}
.is-disable {
  cursor: not-allowed;
}
.is-disable .card-front {
  filter: grayscale(100%);
}

.card .card-front,
.card .card-back {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 1rem;
}

.card .card-front {
  background: var(--theme-card-bg);
  color: coral;
}

.card .card-back {
  background: var(--theme-card-bg-back);
  color: var(--theme-font-main);
  transform: rotateY(180deg);
}

.title {
  text-align: center;
  font-size: 12px;
  color: var(--theme-font-main);
  margin-top: 6px;
  transition: transform 0.8s;
}
</style>
