<template>
  <basic-card
    :isFlipped="isFlipped"
    :isDisable="true"
    title="分数"
    size="small"
  >
    <template v-slot:front>
      <p>💯</p>
    </template>
    <template v-slot:back>
      <div class="score-card-back">
        <div class="highest">🏆{{ topScore }}</div>
        <div class="curren">{{ currentScore }}</div>
      </div>
    </template>
  </basic-card>
</template>

<script>
import BasicCard from "../game-card/basic-card.vue";
export default {
  name: "theme-card",
  components: { BasicCard },
  data() {
    return {
      topScore: null,
    };
  },
  created() {
    // 在组件创建时初始化 store
    const savedTopScore = localStorage.getItem("topScore");
    this.topScore = savedTopScore ? savedTopScore : 0;
  },

  methods: {
    handleCardFlip() {
      console.log("TUDO");
    },
  },
  computed: {
    //游戏中不允许设置
    isFlipped() {
      return this.$store.game.playing;
    },
    // 获取当前分数
    currentScore() {
      return this.$store.score.currentScore
        ? this.$store.score.currentScore
        : 0;
    },
  },
};
</script>

<style scped>
.score-card-back {
  position: relative;
  font-size: 16px;
}
.highest {
  position: absolute;
  top: 15px;
  right: 20px;
}
.curren {
  margin-top: 20px;
  font-size: 36px;
  font-weight: 800;
  line-height: 80px;
}
</style>
